import React from 'react'
import styles from "./Community.module.css"
import {useDispatch, useSelector} from "react-redux";
import {ChangeTextarea, Follow, SearchingCommunities, unFollow} from "../../redux/CommunityReducer";
import {Field, reduxForm} from "redux-form";
import {Input} from "../common/FormsControl/FormsControl";
import {required} from "../../utils/validators/validators";
import CommunityItem from "./CommunityItem/CommunityItem";


const CommunityReduxForm = reduxForm({form: "community"})((props) => {
    return <form onSubmit={props.handlerSubmit}>
        <Field name={"SearchingText"} component={Input} className={styles.BoxSearching} placeholder="Community"
               validate={[required]}/>
        <button className={styles.Searches}>
            Search
        </button>
    </form>
})

function Community() {
    const Communities = useSelector(state => state.CommunityPage.Communities)
    const dispatch = useDispatch()

    function SubmitSearchCommunity(values) {
        dispatch(SearchingCommunities(values.SearchingText))
    }

    return <div className={styles.CommunityContainer}>
        <CommunityReduxForm onSubmit={SubmitSearchCommunity}/>
        {Communities.map(item => {
            return <CommunityItem CommunityName={item.CommunityName} Description={item.Description}
                           CommunitiesPhoto={item.CommunitiesPhoto} isFollowed={item.isFollowed}
                           id={item.id} unFollow={(id) => {dispatch(unFollow(id))}}
                           Follow={(id) => {dispatch(Follow(id))}} SubscribesValue={item.SubscribesValue}/>
            })
        }
    </div>
}

export default Community;