import React from 'react';
import styles from "./CommunityItem.module.css";

function CommunityItem(props) {
    return <div className={styles.Communities}>
                    <span className={styles.CommunityName}>
                        {props.CommunityName}
                    </span>
        <div className={styles.CommunityDescription}>
            {props.Description}
        </div>
        <span>
            Followers: {props.SubscribesValue}
        </span>
        <span>
            <img src={props.CommunitiesPhoto != null ? props.CommunitiesPhoto : null}/>
            <div>
                {!props.isFollowed ? <button onClick={() => {
                    props.Follow(props.id)
                }}>
                    Follow
                </button> : <button onClick={() => {
                    props.unFollow(props.id)
                }}>Unfollow</button>}
            </div>
        </span>
    </div>
}

export default CommunityItem;